import styled from 'styled-components';

export const StyledDescription = styled.div`
  width: 90%;
  max-width: 520px;
  margin: 0 auto;
  @media (min-width: 768px) {
    margin-left: 3rem;
  }
`;

StyledDescription.displayName = 'StyledDescription';

export const StyledLabel = styled.span`
  color: rgb(184, 184, 184);
`;

StyledLabel.displayName = 'StyledLabel';

export const StyledList = styled.p`
  margin: 0;
`;

StyledList.displayName = 'StyledList';

export const StyledRow = styled.div`
  margin-top: 2rem;
  display: flex;
  justify-content: flex-start;
  text-align: left;
`;

StyledRow.displayName = 'StyledRow';

export const StyledRowLeft = styled.div`
  width: 7rem;
`;

StyledRow.displayName = 'StyledRowLeft';

export const StyledRowRight = styled.div`
  width: 7rem;
  margin-left: 4rem;
`;

StyledRowRight.displayName = 'StyledRowRight';

export const StyledTagline = styled.p`
  color: rgb(184, 184, 184);
`;

StyledTagline.displayName = 'StyledTagline';



